import React from 'react';

import './title.css';

function Title() {
    return( 
        <div className="title-parent-div">
        <div className="image">
            <img className="user_image" src="data:image/jpeg;base64," alt="Paste Your Image Here" height="100px" width="100px" />
        </div>
     <h1 className="heading1">Contact Form</h1>
     <h5 className="heading2">Please fill all the texts in the fields</h5>
    </div>
    )
}

export default Title;